html{
    overflow: hidden;
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
    height: 100%;
}

body{
    background: blanchedalmond;
    text-align: center;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
}

#root{
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}

#root1{
    text-align: center;
    width:100%;
    height: 100%;
    padding: 0;
    margin: 0;
}

#root2{
    text-align: center;
    display: flex;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
}

#title{
    margin: 20px;
}

#editor{
    display: flex;
    flex-direction: row;
    left: 0;
    margin-left: 1vw;
    height: 85%;
    width: 45%;
    position: absolute;
}

#preview{
    border-radius: 4px;
    border-color: grey;
    overflow: scroll;
    margin-right: 1vw;
    position: absolute;
    border-style: solid;
    padding: 1vw;
    text-align: left;
    right: 1vw;
    height: 85%;
    width: 45%;
    border-width: 2px;
}
#preview::-webkit-scrollbar{
    display: none;
}

#editor{
    margin-left: 1vw;
    left: 1vw;
    position: absolute;
    border-radius: 4px;
    font-size: 14px;
    font-family: monospace;
    background: blanchedalmond;
    padding: 1vw;
    width: 45%;
    height: 85%;
    border: grey solid;
    border-width: 2px;
}
#editor::-webkit-scrollbar{
    display: none;
}
#editor:focus{
    outline: none !important;
}

h1{
    margin: 0;
    padding: 0;
}

table ,th ,td{
    border-collapse: collapse;
    border-color: black;
    border-style: solid;
    border-width: 3px;
    padding: 5px;
}

img{
    max-width: 100%;
    height: auto;
}


@media screen and (max-width: 768px){
    #root2{
        display: flex;
        flex-direction: column;
        align-items: center;

    }

    #editor{
        left: auto;
        right: auto;
        padding: 0;
        margin: 0 auto;
        height: 44%;
        width: 95vw;
        position: absolute;
    }

    #preview{
        left: auto;
        right: auto;
        padding: 0;
        margin: 0 auto;
        height: 44%;
        width: 95vw;
        bottom: 1vh;
        position: absolute;
    }
}